body {
  background: linear-gradient(to left, rgb(192, 192, 192), #27282e);
}

.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  padding: 20px;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
}

.titulo_header {
  margin-left: 15px;
  font-size: 1.5em;
  font-weight: bold;
}

.content {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 20px;
  gap: 20px;
  
}

.nomelista {
  box-shadow: inset 0 0 10px rgba(0, 0, 0, 0.1);
  padding: 5px;
  background-color: #c5c4c4;
  border-radius: 15px;
  max-width: 60%;
  margin-left: 20%;
}

ul {
  padding: 0;
  margin: 0;
  width: 95%;
  list-style-type: none;
  text-align: left;
  margin-left: 3%;
}

ul li {
  padding: 10px;
  margin: 5px 0;
  background-color: #a3a2ab;
  cursor: pointer;
  transition: background-color 0.3s ease;
  font-size: 1.1em;
  border-radius: 5px;
}

ul li:hover {
  background-color: #8f8ea0;
}

.listacanais {
  background-color: #282c34;

}

.toggle-source-btn {
  background-color: #f0f0f0;
  box-shadow: inset 0 0 10px rgba(0, 0, 0, 0.1);
  border-radius: 15px;
  margin-bottom: 2%;
}

.video-container {
  width: 68%;
  position: fixed; /* Mantém o player fixo ao rolar a página */
  right: 20px;
  top: 100px; /* Distância do topo da página */
  margin-top: 3%;
  
}

.video-descricao {
  display: flex;
  gap: 5%;
  background-color: #f0f0f0;
}

video {
  width: 90%;
  height: auto;
  border: 1px solid #ccc;
  border-radius: 10px;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
}

.search-bar {
  padding: 5px 10px;
  border-radius: 15px;
  margin-bottom: 3%;
  border: 1px solid #ccc;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.3);
  background-color: #f0f0f0;
  width: 70%;
}


@media (max-width: 768px) {
  .content {
    flex-direction: column;
    align-items: center;
  }

  ul {
    width: 100%;
    margin-left: 0;
  }

  .video-container {
    width: 100%;
    position: relative; /* Video volta a ser relativo em telas menores */
    right: 0;
    top: 0;
  }

  video {
    width: 100%;
  }
}
